"use client";

import { useMemo, useRef, useState } from "react";
import { motion } from "motion/react";
import {
  Copy,
  ImageIcon,
  Sparkles,
  Paintbrush,
  MonitorSmartphone,
  Zap,
  RotateCcw
} from "lucide-react";

const MODE_META = {
  art: {
    label: "Art Thief",
    icon: Paintbrush,
    tone: "Focus on lighting, camera, style, mood.",
    badge: "Midjourney / SD"
  },
  ui: {
    label: "UI Pirate",
    icon: MonitorSmartphone,
    tone: "Focus on layout, components, typography, color.",
    badge: "Figma / Web"
  }
} as const;

type Mode = keyof typeof MODE_META;

export default function Home() {
  const [mode, setMode] = useState<Mode>("art");
  const [file, setFile] = useState<File | null>(null);
  const [previewUrl, setPreviewUrl] = useState<string | null>(null);
  const [prompt, setPrompt] = useState<string>("");
  const [status, setStatus] = useState<"idle" | "loading" | "done" | "error">("idle");
  const [error, setError] = useState<string | null>(null);
  const [copied, setCopied] = useState(false);
  const inputRef = useRef<HTMLInputElement>(null);

  const meta = MODE_META[mode];
  const MetaIcon = meta.icon;

  const hint = useMemo(() => {
    if (status === "loading") return "Jacking the prompt...";
    if (status === "done") return "Recipe secured.";
    if (status === "error") return "Heist failed.";
    return "Upload a target image to begin.";
  }, [status]);

  const onPickFile = () => {
    inputRef.current?.click();
  };

  const onFileChange = (selected?: File | null) => {
    if (!selected) return;
    if (!selected.type.startsWith("image/")) {
      setError("Please upload a PNG or JPG image.");
      setStatus("error");
      return;
    }
    setFile(selected);
    setPreviewUrl(URL.createObjectURL(selected));
    setPrompt("");
    setStatus("idle");
    setError(null);
  };

  const handleDrop = (event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    const dropped = event.dataTransfer.files?.[0];
    onFileChange(dropped);
  };

  const handleSubmit = async () => {
    if (!file) {
      setError("Drop an image to reverse-engineer.");
      setStatus("error");
      return;
    }

    setStatus("loading");
    setError(null);
    setCopied(false);

    try {
      const formData = new FormData();
      formData.append("file", file);
      formData.append("mode", mode);

      const response = await fetch("/api/reverse-engineer", {
        method: "POST",
        body: formData
      });

      const data = await response.json();
      if (!response.ok) {
        throw new Error(data?.error || "Unknown error");
      }

      setPrompt(data.prompt || "");
      setStatus("done");
    } catch (err) {
      const message = err instanceof Error ? err.message : "Unexpected error.";
      setError(message);
      setStatus("error");
    }
  };

  const handleCopy = async () => {
    if (!prompt) return;
    await navigator.clipboard.writeText(prompt);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  return (
    <div className="min-h-screen px-6 pb-20 pt-12">
      <header className="mx-auto flex w-full max-w-6xl flex-col gap-4">
        <div className="tag w-fit">
          <Zap className="h-3.5 w-3.5" />
          Reverse-Engineering Lab
        </div>
        <div className="flex flex-col gap-4 md:flex-row md:items-end md:justify-between">
          <div className="space-y-3">
            <h1 className="font-display text-4xl font-semibold tracking-tight md:text-5xl">
              Prompt-Jacker
            </h1>
            <p className="text-lg text-mist">Don&apos;t just admire the view. Steal the recipe.</p>
          </div>
          <div className="glass flex items-center gap-3 rounded-full px-4 py-3 text-sm text-mist shadow-glow">
            <Sparkles className="h-4 w-4 text-brass" />
            The inspect element for visuals
          </div>
        </div>
      </header>

      <main className="mx-auto mt-10 grid w-full max-w-6xl gap-8 lg:grid-cols-[1.05fr_0.95fr]">
        <section className="space-y-6">
          <div className="glass rounded-3xl p-6 shadow-glow">
            <div className="flex flex-wrap items-center justify-between gap-4">
              <div>
                <p className="text-xs uppercase tracking-[0.35em] text-mist">Intent</p>
                <h2 className="mt-2 font-display text-2xl">Choose your heist mode</h2>
              </div>
              <div className="flex items-center gap-2 rounded-full border border-white/10 bg-black/40 p-1">
                {(["art", "ui"] as Mode[]).map((key) => {
                  const Icon = MODE_META[key].icon;
                  const active = key === mode;
                  return (
                    <button
                      key={key}
                      type="button"
                      onClick={() => setMode(key)}
                      className={`flex items-center gap-2 rounded-full px-4 py-2 text-sm transition ${
                        active
                          ? "bg-white text-black shadow-neon"
                          : "text-mist hover:text-white"
                      }`}
                    >
                      <Icon className="h-4 w-4" />
                      {MODE_META[key].label}
                    </button>
                  );
                })}
              </div>
            </div>
            <div className="mt-4 flex flex-wrap items-center gap-3 text-sm text-mist">
              <span className="rounded-full border border-white/10 px-3 py-1 text-xs uppercase tracking-[0.2em]">
                {meta.badge}
              </span>
              <span>{meta.tone}</span>
            </div>
          </div>

          <div
            className="glass relative overflow-hidden rounded-3xl p-6 shadow-glow"
            onDragOver={(event) => event.preventDefault()}
            onDrop={handleDrop}
          >
            <div className="flex items-center justify-between">
              <div>
                <p className="text-xs uppercase tracking-[0.35em] text-mist">Target</p>
                <h3 className="mt-2 font-display text-xl">Drop the reference image</h3>
              </div>
              <button
                type="button"
                onClick={onPickFile}
                className="rounded-full border border-white/10 px-4 py-2 text-xs uppercase tracking-[0.2em] text-mist transition hover:border-white/40 hover:text-white"
              >
                Browse
              </button>
            </div>

            <div className="mt-6 grid gap-4 md:grid-cols-[1.1fr_0.9fr]">
              <div className="relative rounded-2xl border border-dashed border-white/20 bg-black/40 p-5">
                {previewUrl ? (
                  <img
                    src={previewUrl}
                    alt="Uploaded preview"
                    className="h-64 w-full rounded-xl object-cover"
                  />
                ) : (
                  <div className="flex h-64 flex-col items-center justify-center gap-3 text-mist">
                    <ImageIcon className="h-8 w-8" />
                    <p className="text-sm">PNG or JPG up to 8MB</p>
                  </div>
                )}

                {status === "loading" && (
                  <div className="pointer-events-none absolute inset-0">
                    <motion.div
                      className="absolute inset-0 bg-gradient-to-b from-transparent via-white/20 to-transparent"
                      animate={{ y: ["-120%", "120%"] }}
                      transition={{ duration: 2.5, repeat: Infinity, ease: "linear" }}
                    />
                  </div>
                )}
              </div>

              <div className="flex flex-col justify-between gap-4">
                <div className="rounded-2xl border border-white/10 bg-black/30 p-4 text-sm text-mist">
                  <p className="font-display text-lg text-white">How it works</p>
                  <ul className="mt-3 space-y-2">
                    <li>1. Upload a reference image.</li>
                    <li>2. Pick Art or UI mode.</li>
                    <li>3. We extract the recipe.</li>
                  </ul>
                </div>
                <button
                  type="button"
                  onClick={handleSubmit}
                  className="rounded-2xl bg-brass px-5 py-4 text-sm font-semibold uppercase tracking-[0.2em] text-black transition hover:translate-y-[-2px] hover:shadow-neon"
                >
                  Start the heist
                </button>
              </div>
            </div>

            <input
              ref={inputRef}
              type="file"
              accept="image/*"
              className="hidden"
              onChange={(event) => onFileChange(event.target.files?.[0] || null)}
            />
          </div>
        </section>

        <section className="space-y-6">
          <div className="glass rounded-3xl p-6 shadow-glow">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-xs uppercase tracking-[0.35em] text-mist">Output</p>
                <h3 className="mt-2 font-display text-2xl">The stolen prompt</h3>
              </div>
              <span className="text-xs uppercase tracking-[0.3em] text-mist">{hint}</span>
            </div>

            <div className="mt-4 rounded-2xl border border-white/10 bg-black/40 p-5">
              <p className="whitespace-pre-wrap font-mono text-sm text-white/90">
                {prompt ||
                  "Cinematic lighting, 35mm lens, hyper-detailed portrait, teal shadows, golden rim light, Unreal Engine render, glossy reflections..."}
              </p>
            </div>

            {error && (
              <p className="mt-3 text-sm text-mag">{error}</p>
            )}

            <div className="mt-5 flex flex-wrap items-center gap-3">
              <button
                type="button"
                onClick={handleCopy}
                className="flex items-center gap-2 rounded-full border border-white/10 px-4 py-2 text-xs uppercase tracking-[0.2em] text-mist transition hover:border-white/40 hover:text-white"
              >
                <Copy className="h-4 w-4" />
                {copied ? "Copied" : "Copy"}
              </button>
              <button
                type="button"
                disabled
                className="flex items-center gap-2 rounded-full border border-white/10 px-4 py-2 text-xs uppercase tracking-[0.2em] text-mist/60"
              >
                <RotateCcw className="h-4 w-4" />
                Remix (Soon)
              </button>
            </div>
          </div>

          <div className="glass rounded-3xl p-6 shadow-glow">
            <div className="flex items-center gap-3">
              <div className="flex h-12 w-12 items-center justify-center rounded-2xl bg-white/10">
                <MetaIcon className="h-6 w-6 text-brass" />
              </div>
              <div>
                <p className="text-xs uppercase tracking-[0.3em] text-mist">Selected Engine</p>
                <p className="font-display text-lg text-white">{meta.label}</p>
              </div>
            </div>
            <p className="mt-4 text-sm text-mist">
              {mode === "art"
                ? "Expect camera metadata, lighting, rendering engines, and dense style tags."
                : "Expect layout primitives, UI component language, typography stacks, and palette hierarchy."}
            </p>
          </div>
        </section>
      </main>
    </div>
  );
}
